.fav-doc {
  position: absolute;
  height: 100%;
  background: #ffffff;
  width: 100%;
  color: #186895;
  overflow-x: hidden;
  z-index: 10;
  padding: 50px 70px 50px 50px;

}
.fav-doc h1 {
  font-size: 36px;
  font-weight: 600;
}
.fav-doc p {
  font-size: 20px;
  line-height: 30px;
}
.fav-doc .name {
  width: 1000px;
  margin: 40px 0;
  display: flex;
  justify-content: space-between;
}
.fav-doc h3 {
  font-size: 20px;
  font-weight: 600;
}
.fav-doc .cancel {
  border: 2px solid #186895;
  height: 30px;
  width: 30px;
  border-radius: 20px;
  margin-left: 100%;
  cursor: pointer;
}
.first-second {
  margin-top: 23px;
}
.street-name {
  margin-top: 30px;
}
.user-cont {
  margin: 60px 0 0 0;
}

.user-cont input,
.mail input {
  width: 334px;
  height: 55px;
  border-radius: 10px;
  border: 1px solid #186895;
}
.name input,
.date-input input {
  width: 250px;
  height: 55px;
  border-radius: 10px;
  border: 1px solid #186895;
}
.fav-doc input {
  padding: 20px;
  font-family: inherit;
  cursor: text;
}
.form p {
  font-size: 18px;
}
.mail {
  display: flex;
  width: 800px;
  justify-content: space-between;
}
.address {
  display: flex;
  margin-bottom: 30px;
}
.address-box {
  margin-top: 70px;
}
.add-box {
  height: 55px;
  width: 220px;
  border: 1px solid #186895;
  border-radius: 10px 0 0 10px;
}
.street-box {
  width: 450px;
  border: 1px solid #186895;
  border-radius: 0 10px 10px 0;
  height: 55px;
}
.birth-date .date-input {
  display: flex;
  justify-content: space-between;
  width: 1000px;
}
.btn {
  display: flex;
  justify-content: space-between;
}
.btn input,
.blood input,
.phone-radio input {
  cursor: pointer;
}
.buttons {
  width: 200px;
  margin-bottom: 40px;
}
.radio-btn {
  display: flex;
}
.radio-btn input {
  margin-right: 10px;
  transform: translateY(9px);
}
.blood-group {
  width: 1000px;
}
.blood {
  margin-bottom: 40px;
}
.gen-btn,
.marital-btn {
  width: 700px;
  margin-bottom: 30px;
}
.fav-doc textarea {
  width: 471px;
  padding: 20px;
  font-family: inherit;
  height: 172px;
  border: 1px solid #186895;
  border-radius: 20px;
  margin: 20px 0 40px 0;
}
.notes input {
  width: 471px;
  border: 1px solid #186895;
  border-radius: 20px;
  margin: 20px 0 40px 0;
}
.phone-type {
  display: flex;
}
.phone-radio p {
  font-size: 18px;
  transform: translateY(11px);
}
.phone-radio {
  display: flex;
  justify-content: space-between;
  width: 100px;
}
.notes .button {
  background: #186895;
  color: #ffffff;
  border-radius: 5px;
  font-family: inherit;
  border: none;
  cursor: pointer;
  text-align: center;
  padding: 10px;
  width: 150px;
  margin: 30px auto;
}
.notes .button:hover {
  background: #498cb3;
  text-decoration: none;
  transition: all 0.6s ease-in;
  box-shadow: 7px 9px 28px -5px rgba(36, 30, 30, 0.5);
  -webkit-box-shadow: 7px 9px 28px -5px rgba(36, 30, 30, 0.5);
  -moz-box-shadow: 7px 9px 28px -5px rgba(36, 30, 30, 0.5);
}
@media screen and (max-width: 1024px) {
  .fav-doc {
    padding: 50px 50px 50px 40px;
  }
  .fav-doc .name {
    flex-direction: column;
  }
  .fav-doc .welcome {
    width: 700px;
  }
  .fav-doc .mail,
  .blood-group {
    width: 700px;
  }
  .gen-btn,
  .marital-btn {
    width: 600px;
  }
  .form p {
    font-size: 15px;
  }
}
@media screen and (max-width: 500px) {
  .fav-doc {
    padding: 50px 50px 50px 30px;
  }
  .fav-doc .mail,
  .date-input,
  .address,
  .gen-btn,
  .marital-btn {
    flex-direction: column;
  }
  .mail .number {
    margin-top: 30px;
  }
  .street-box {
    width: 350px;
  }
  .blood-group .btn {
    flex-direction: column;
    margin-bottom: 30px;
  }
  .fav-doc textarea,
  .notes input {
    width: 350px;
  }
  .fav-doc .welcome{
    width: 350px;
  }
  .fav-doc h1{
    font-size: 30px;
  }
  .welcome p{
    font-size: 18px;
  }
  .genotype input,
  .blood-group input,
  .gender input,
  .marital-status input{
    height: 9px;
    transform: translateY(10px);
  }
  .phone-radio input{
    height: 9px;
    transform: translateY(3px);
  }
}
@media screen and (max-width: 380px) {
   .welcome p{
     font-size: 16px;
   }
  .street-box {
    width: 340px;
  }
  .fav-doc {
    padding: 50px 50px 50px 20px;
  }
  .fav-doc textarea,
  .notes input {
    width: 340px;
  }
}
